<script lang="ts">
  import {
    apple,
    bandcamp,
    instagram,
    minus,
    plus,
    share,
    soundcloud,
    spotify,
    youtubePlay
  } from 'svelte-awesome/icons'
  import Icon from 'svelte-awesome'
  import { hasLinks } from '../helpers'
  import { Share } from '@capacitor/share'
  import { Capacitor } from '@capacitor/core'
  import { getAccount } from '../api/account'
  import { alertTimeout, inArray, openSite } from '../helpers'
  import { account, alert, authenticated, page } from '../stores'
  import { addToLibrary, getLibrary, removeFromLibrary } from '../api/artists'

  export let artist: {
    uid?: string
    name: string
    genres?: string
    location?: string
    links?: any
  }
  let { name, location, links, genres } = artist
  let artistURL = `https://app.tapedrop.com?artist=${artist.uid}`
  let added = !inArray(artist.uid, $account.library)

  function onShare() {
    if (Capacitor.isNativePlatform()) {
      Share.share({
        title: `${artist.name} on Tapedrop`,
        text: `${artist.name} on Tapedrop`,
        url: artistURL,
        dialogTitle: 'Share with friends'
      })
    } else {
      navigator.clipboard.writeText(artistURL)
      alert.set({ class: 'alert-success', text: 'Link Copied' })
      alertTimeout()
    }
  }
  async function handleAdd() {
    if (!$authenticated) {
      page.set('login')
      return
    }
    await addToLibrary(artist.uid)
    added = !added
    getAccount($account.uid)
    getLibrary()
  }
  async function handleRemove() {
    await removeFromLibrary(artist.uid)
    added = !added
    getAccount($account.uid)
    getLibrary()
  }
</script>

<div class="card border mx-auto my-20">
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={onShare}>
    <Icon class="share" data={share} />
  </a>
  {#if artist.uid != $account.uid}
    {#if added}
      <!-- svelte-ignore a11y-invalid-attribute -->
      <a href="#" on:click={handleAdd}>
        <Icon class="add-or-remove" data={plus} />
      </a>
    {/if}
    {#if !added}
      <!-- svelte-ignore a11y-invalid-attribute -->
      <a href="#" on:click={handleRemove}>
        <Icon class="add-or-remove" data={minus} />
      </a>
    {/if}
  {/if}
  <div>
    <div class="row justify-content-center">
      <!-- svelte-ignore a11y-invalid-attribute -->
      <a href={`/?artist=${artist.uid}`}>
        <h1 class="card-title">
          {name}
        </h1>
      </a>
    </div>
    {#if genres}
      <div class="text-center m-auto">
        {genres.toUpperCase()}
      </div>
    {/if}
    <div class="row justify-content-center social">
      {#if !hasLinks(artist)}
        <p class="text-center">
          Welcome to Tapedrop! Edit your profile and add music links to show up
          in searches.
        </p>
      {/if}
      {#if links.spotify}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.spotify)}>
          <Icon class="spotify" data={spotify} />
        </a>
      {/if}
      {#if links.apple}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.apple)}>
          <Icon class="apple" data={apple} />
        </a>
      {/if}
      {#if links.bandcamp}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.bandcamp)}>
          <Icon class="bandcamp" data={bandcamp} />
        </a>
      {/if}
      {#if links.soundcloud}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.soundcloud)}>
          <Icon class="soundcloud" data={soundcloud} />
        </a>
      {/if}
      {#if links.instagram}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.instagram)}>
          <Icon class="instagram" data={instagram} />
        </a>
      {/if}
      {#if links.youtube}
        <!-- svelte-ignore a11y-invalid-attribute -->
        <a href="#" on:click={() => openSite(links.youtube)}>
          <Icon class="youtube" data={youtubePlay} />
        </a>
      {/if}
    </div>
    {#if location}
      <p class="text-center text-muted">Based in {location}</p>{/if}
  </div>
</div>
